import React from 'react';
import axios from 'axios'
import Movie from './Movie.js'
import './App.css'
import './Movie.css'

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  }

  getMovies = async () => {
    const {data: {data: {movies}}} = await axios.get('https://yts-proxy.now.sh/list_movies.json?sort_by=rating')
    this.setState({ movies, isLoading: false})
  }

  componentDidMount() {
    // setTimeout(() => {
    //   this.setState({isLoading: false})
    // }, 6000)
    this.getMovies()
  }
  
  render() {
    const { isLoading, movies } = this.state
    return (
      <React.Fragment>
        <h1 className="app__title">Movies!</h1>
        <div className="app__content">
          {isLoading ? (
            <span className="txt_loading">loading...</span>
            ) : (
              <div className="wrap_movies">
                {movies.map(({ id, title, year, summary, medium_cover_image, genres }) => (
                  <Movie
                  key={id}
                  id={id}
                  title={title}
                  year={year}
                  summary={summary}
                  poster={medium_cover_image}
                  genres={genres}
                  />
                ))}
            </div>
          )}
        </div>
      </React.Fragment>
    )
  }
}

export default App;
